import React, { useContext } from 'react';
import { format, parseISO } from 'date-fns';
import { DayNightContext } from '../components/DayNightContext';

interface PostComponentWithMetadata extends React.FC {
  metadata?: {
    title: string;
    date: string;
  };
}

const DATE = "2024-08-24";
const TITLE = "Un astronauta más allá de las estrellas";


const PostComponent: PostComponentWithMetadata = () => {
  const { isDay } = useContext(DayNightContext);

  const formattedDate = format(parseISO(DATE), 'dd-MM-yyyy');

  return (
    <div className={`max-w-4xl mx-auto rounded-md p-4 border-l-4 ${isDay ? 'bg-primary text-secondary border-secondary' : 'bg-secondary text-primary border-primary'}`}>
      <h1 className={`text-4xl font-bold mb-2 ${isDay ? 'text-secondary' : 'text-primary'}`}>{TITLE}</h1>
      <p className="text-sm text-gray-500">{formattedDate}</p>
      <p className="text-sm mb-10 mt-5 text-gray-500">Canción sugerida al leer: <i>"The Earth Prelude"-Ludovico Einaudi</i></p>
      
      <p className="mb-4"> Cuando era niño, solía mirar las estrellas, impactado, asombrado e incluso completamente silenciado cuando la inmensidad del cosmos me minimizaba con su majestuosidad. Poco después de haber contemplado por primera vez la inmensidad del cosmos desde el patio de mi casa, lo primero que pensé fue: <strong>¿Qué existe allá afuera?</strong> Sin embargo, no era una pregunta tan fácil de responder. Mi curiosidad me llevó a la biblioteca de mi escuela primaria "20 de Noviembre", donde conseguí un libro que, según recuerdo, se llamaba "El sistema solar y el cosmos". Aunque la portada mostraba el sistema solar, si mi memoria no me falla, el contenido se enfocaba principalmente en el sistema solar. Recuerdo que mencionaban que Plutón era el planeta más pequeño del sistema, lo cual me impactó enormemente. Cada imagen que veía en el libro me hacía reflexionar sobre lo poco que conocía de lo que significa vivir. Entonces, decidí que cuando me preguntaran sobre mi futura profesión, respondería <strong>astronauta</strong>, con la intención de satisfacer mi curiosidad de saber: <strong>¿Qué más hay allá afuera?</strong>. Recuerdo que la gente solía decir que era muy difícil, pero que lo podría lograr; en su mayoría, quienes me alentaban eran familiares, por lo que sentía al menos un poco de apoyo. Tanto así que un tío solía invitarme a ver la serie <strong>"Cosmos"</strong> de Neil DeGrasse Tyson, cuya personalidad, incluso en su edad adulta al momento de haber hecho la serie, se comparaba con la mía: parecía que nada podía satisfacer esa sed de conocer el mundo.</p>

      <p className='mb-4'>Crecí bajo la influencia de personas que, desde niño, fueron una fuente tremenda de inspiración en el ámbito del conocimiento. Recuerdo que, cuando le preguntaba algo a mi padre, él intentaba responderme de manera que me hiciera ver que su respuesta era solo una entre muchas posibles. Siempre me dejaba con más preguntas que respuestas, cosa que, con el tiempo, entendí que fue lo mejor que pudo haber hecho conmigo. Después, un primo al que llamaré George fue una persona que me llevaba algunos años. Sin embargo, cada vez que lo veía, me deleitaba con un tema nuevo. Solía hablar por horas sobre temas variados, llenos de ciencia o incluso misticismo, pero su forma de hablar era lo que siempre admiré, porque conocer era lo que lo motivaba. Solía investigar sobre algo cuando no lo sabía, especialmente si era algo que yo conocía y él no. Y no lo culpo, todos los que tenemos un poco de esta chispa sentimos que no es cuestión de que otro sepa más que nosotros, sino que impresiona tanto que se siente como si nos estuviéramos perdiendo de algo. Otra persona importante para mí fue un tío muy cercano a mi papá y mi mamá, pero que dejó una huella que de niño me dolió mucho hasta que la entendí. Me dijo: "No puedes enseñarle nada a un adulto". El contexto de esa plática surgió porque, cuando era muy niño, solía tener buenas calificaciones, tanto que un 9 me pesaba lo mismo que un 6; para mí era intolerable. Un día, en una fiesta familiar a la que no quería ir porque al día siguiente tenía escuela, mientras mi madre intentaba despejarse del estrés de la rutina, me encontré en una reunión llena de alcohol y cigarrillos. Ante mis ojos, solo veía personas intentando ignorar la realidad, algo que iba muy en contra de mi recién nacida filosofía de buscar más allá de lo que mis ojos podían ver y por ese motivo me acerque a mi tío a pedirle que si le decía algo a mi madre. Entonces, durante los siguientes 8 años, decidí seguir aprendiendo de todo y maravillándome con cada cosa, preguntando, explorando. En esa etapa vivía a las afueras de la ciudad, a 40 minutos, sin vecinos, donde solo éramos mi hermano y yo contra el mundo. Jugábamos, cavábamos, llenábamos nuestras ropas de lodo al andar en bicicleta en época de lluvias, coleccionábamos caracoles en botellas de Coca-Cola y hacíamos de nuestro patio un mundo lleno de posibilidades. Después de aquel comentario de mi tío, mi lucha fue convertir su afirmación en una promesa contra la ignorancia. Cabe aclarar que, al decir "ignorancia" en este contexto, me refiero a las cosas que ignoramos o no conocemos, muchas veces no por elección, sino porque se pierden de nuestro campo de visión.</p>


    <p className='mb-4'>La magia es increíble, literalmente, porque cuando descubres lo que hay detrás del truco, deja de ser interesante. Así lo creí durante mi época de preparatoria, donde mi hiperrealismo contaminó mis sueños de niño, entendiendo que ser astronauta era solo un camino que quería continuar más por capricho que porque realmente tuviera alguna posibilidad. Entre mis amistades, cuando llegaba a escuchar lo que querían ser, nada sonaba con el esmero o la pasión con la que yo solía hablar. Todo sonaba tan monótono e incluso desmoralizador, que nunca tuve la oportunidad de reconsiderar el volver a plantearme ser astronauta, hasta tiempo después. Durante la preparatoria, ese hiperrealismo me llevó a dejar de soñar, dejar de imaginar, dejar de soñar despierto, dejar de <strong>mirar las estrellas</strong>, algo que solía hacer mucho cuando era niño. Lo que en mi historia de vida llamo "<strong>Mi edad media</strong>", ya que sentí un estancamiento total en mi vida. Tanto así que de alguna manera desarrollé una manía por resolver los puzzles mentales de cada persona, haciéndome a la idea de que podía conocerlos aprendiendo de su historia de vida. Así, poco a poco, lograba llegar con cada una de esas personas y listar mis descubrimientos acerca de ellas, al punto en que incluso disfrutaba ver cómo su expresión se tornaba molesta al saber que había acertado. Una mala copia de Dr. Gregory House podríamos decir. Pero sabía, muy en el fondo, que no quería descubrir secretos de las personas, solo que aún no sabía por dónde buscar. Quería algo más. Aparte de eso, me volví una persona odiosa y perdí amistades de manera justificada. Pero toda luz tiene una gran sombra, sombra que jamás ocultaré. Sería estúpido pensar que lo que soy hoy es gracias a una serie de situaciones y momentos limpios, perfectos y precisos, cuando es totalmente lo contrario. Soy una serie de incongruencias y desperfectos que, con el tiempo, moldeé para darle vida a mi yo actual, aceptando esta época de oscuridad en mi saber y entendiendo que decir <strong>¿Qué más hay allá afuera?</strong> era solo una pequeña parte de lo que podía hacer, y que mirar afuera no era necesariamente la única manera que detonaba la curiosidad.</p>

    <p className='mb-4'>Mi hermano suele decir de broma que Canadá es para mí lo que Vietnam fue para Estados Unidos, y todo esto viene por una serie de eventos. Pero lo que realmente encontré fue la segunda pregunta que detonaba una curiosidad sin igual en mí, tanto que se me erizaba la piel al hacerme esta pregunta. Y puede sonar raro para muchos; no los culpo. Yo también solía pensar que el mundo que me rodeaba era todo lo que conocía, y con eso bastaba. Hubo dos ingredientes para este descubrimiento: estar <strong>completamente solo</strong> y <strong>caminar tanto como pudiera</strong>. Estos dos ingredientes hicieron algo muy curioso; lo primero fue que estar en un país donde las costumbres, paisajes, arquitectura, flora, fauna, comida y, principalmente, el idioma eran diferentes, me hacía sentir muy lejos de casa. Y si a ese sentimiento le sumas el hecho de que realmente estás solo, pues, aunque las personas a tu alrededor se cuenten por docenas o cientos, basta con pensar que nadie te mira. Peor aún, en algunas aventuras que tuve en los bosques que rodeaban la ciudad, no solo se trataba de un sentimiento, sino que realmente había momentos en los que pasaban 40 minutos sin que viera a una persona desde que me adentraba en los bosques llenos de arces y pinos junto con esa maleza hermosa que aún recuerdo. Y aquí es donde se junta el segundo ingrediente. Mencioné 40 minutos, pero ese no era el tiempo que caminaba en un día; caminaba un promedio de 29 kilómetros por día y, en los mejores días, hasta 32, que fue mi récord. Eran horas de caminar, caminar y caminar. Es necesario mencionar que, cuando uno va en carretera en un viaje de 2 o 3 horas, el paisaje cambia tan rápido que no nos da tiempo de ver detalles. Y mucho menos si miramos de cerca, porque las líneas discontinuas se vuelven continuas dependiendo de la velocidad a la que vamos. Pero seguro que no queremos que un viaje de 180 kilómetros nos tome un día cuando podemos hacerlo en 3 horas, ¿cierto? Bueno, caminar tales distancias no presenta ese problema; tanto que surge otro después de desmenuzar el paisaje, desde las construcciones hasta las piedras que sobresalen del concreto en la calle o la textura de las hojas al caminar sobre ellas. Incluso llegaba a comparar las copas de los árboles para saber cuál es más alto y, posiblemente, más viejo. Llega un momento en el que la mente deja de ver hacia afuera y es cuando dejé de preguntarme <strong>¿Qué hay allá afuera?</strong> y pasé a preguntarme <strong>¿Qué hay aquí adentro?</strong>. Esa pregunta lo cambió todo. Dejar de ver hacia afuera para mirar dentro hizo que pensara que apenas si me conocía. ¡¿Pero cómo?! Si yo he habitado este cuerpo por años y, por obvias razones, jamás me he separado de él. Esto me llevó a ver que dentro de mí había un universo que, aunque no tuviera un traje o una nave espacial, podía visitar de vez en cuando y cada vez más a menudo. Así, la introspección se convirtió en mi pasatiempo favorito, perdiéndome en mis rincones internos. Hay una parte de la canción de Calle 13 que se llama <i>Me vieron cruzar</i> que dice: "Pero soy un barrilete cósmico, lo más grande", la cual fue mi himno ante este descubrimiento. Aunque para ese momento, el rock, que había sido mi género maestro, dejó de gustarme, ya que buscaba algo que acompañara mis pensamientos y me apoyara en mi descubrimiento sin manipulación y sin desviarme de mi introspección, como las canciones acompañan a los grandes films. Fue entonces cuando conocí a Ludovico Einaudi y a Tony Anderson, a quienes estimo porque, gracias a sus complejas notas, pude entender la inmensidad de mi propio desconocimiento. Por segunda ocasión en mi vida, me invadió esa sensación de asombro, majestuosidad e intriga, como cuando solía ver las estrellas de niño, y allí dije nuevamente: <strong>Quiero ser astronauta</strong>. Solo que ahora no hablaba de la profesión, sino de lo que hacen: prepararse toda una vida con un entrenamiento riguroso y especializado para soportar lo que una persona normal no podría, como las presiones G, la movilidad limitada, o la gravedad que se comporta diferente, donde la idea de "lugar para vivir" cambia por completo, donde solo unos pocos podrían llegar y ver con sus propios ojos lo que para otros son solo teorías y conspiraciones. Sé que no son dioses, pero cuando en tiempos antiguos apuntábamos al cielo, muchas veces no era para señalar las estrellas. Yo suelo hacerlo, pero no para saludar a los dioses, sino para recuperar mi rumbo.</p>

    <p>Esto que relato fue el comienzo de cómo pude volver a querer ser ese astronauta, la idea de lo imposible e incluso más allá de las estrellas, y cómo un sueño de niño fue lo que me salvó la vida. Si no me hubiera expuesto a esto, quizá este blog no existiría, y es posible que estaría jodiendo la vida a las personas de manera inconsciente. Y por eso, por si un día olvido este sueño otra vez y vuelve a existir una época oscura, o como yo la llamo <strong>Mi edad media</strong>, solo bastará con ver mis tatuajes. Estos representan esta parte de mi vida y mi descubrimiento: nunca dejar de conocer, aprender, maravillarse y, principalmente, siempre apuntar a las estrellas.</p>

    </div>
  );
};

PostComponent.metadata = {
  title: TITLE,
  date: DATE
};

export default PostComponent;
